<template>
    
    <b-container>
            <b-row>
                <b-col class="text-center">
                    <h5>AIIAS Events Page</h5>
                </b-col>
            </b-row>
            <b-row>
                <b-col class="text-center">
                    <b-form-group
                        label="Filter"
                        label-cols-sm="1"
                        label-align-sm="left"
                        label-size="sm"
                        label-for="filterInput"
                    >
                        <b-input-group size="sm">
                            <b-form-input
                                v-model="filter"
                                type="search"
                                id="filterInput"
                                placeholder="Type to Search"
                            ></b-form-input>
                    
                            <b-input-group-append>
                                <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col>
                    <!-- pagination component  -->
                    <b-pagination
                        v-model="currentPage"
                        :total-rows="rows"
                        :per-page="perPage"
                        aria-controls="my-table-meetings"
                        align="left"
                        label="Sort"
                    >
                    </b-pagination>
                </b-col>
                <b-col class="text-left"> 
                    <p>Current Page: {{ currentPage }}</p>
                </b-col>
                <b-col> 
                    <div class="text-right">
                        <!-- Pass modal that will be visualized and pass the first component to be focused -->
                        <b-button variant="success" size="sm" @click="setEventEdit('0')" v-b-modal.modalEditEvents>
                            Add Event
                        </b-button>
                    </div>
                </b-col>
            
            </b-row>
            <b-row>
                <b-col>
                    <!-- main table -->
                    <b-table
                        class="text-left"
                        id="my-table-events"
                        :items="events"
                        :fields="fields"
                        :per-page="perPage"
                        :current-page="currentPage"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :filter="filter"
                        :filterIncludedFields="filterOn"
                        @filtered="onFiltered"
                        sort-icon-left
                        responsive="sm"
                        small
                    >
                        <!-- Buttons -->
                        <template v-slot:cell(actions)="events">
                            <b-button  variant="dark" class="mr-2" size="sm" ref="btnShow" @click="setEventEdit(events.item.id)" v-b-modal.modalMeetings>
                                Breakout Sessions
                            </b-button>
                            <b-button  variant="primary" class="mr-2" size="sm" @click="setEventEdit(events.item.id)"  v-b-modal.modalEditEvents>
                                Edit
                            </b-button>
                            <b-button  variant="danger" class="mr-2" size="sm" v-on:click="deleteData(events.item.id, events.item.name, events.item.url_image)">
                                Delete
                            </b-button>
                        </template>
                    </b-table>                   
                </b-col>
            </b-row>
            <b-row>
                <b-col class="modal-footer">
                    
                </b-col>
            </b-row>

            <ModalEditEvent  :event="event" />
           <ModalMeeting :event="event"  />
        </b-container>
</template>

<script>
import ModalEditEvent from '../components/ModalEditEvent.vue'
import ModalMeeting from '../components/ModalMeeting.vue'
import firebase from 'firebase'

export default {
    components: {
        ModalEditEvent,
        ModalMeeting
    },
    name: 'ListOfEvents',
    props: ['events', 'event'],

    data() {
        return {
            filter: null,
            filterOn: [],    
            sortBy: 'Name',
            sortDesc: false,
            perPage: 10,
            currentPage: 1,

            fields: [
                {
                    key: 'name_show', 
                    label: 'Name',
                    sortable: true,
                    sortDirection: 'desc'
                },
                {
                    key: 'date_show_to', 
                    label: 'Date To',
                    sortable: true
                },
                {
                    key: 'date_show_from', 
                    label: 'Date From'
                },
                
                {
                    key: 'actions', 
                    label: 'Actions'
                }
            ],
            del:false/*,
            ok_delete: false*/
        }
    },
    computed: {
        rows() {
            return this.events.length
        },
        nameState() { 
            return this.event.name.length > 2 ? true : false
        }
    },
    methods: {
        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRoaws = filteredItems.length
            this.currentPage = 1
        },
        setEventEdit(event_id) {
            this.event.id = event_id
            this.event.name = ''
            this.event.reason = ''
            this.event.date_to = new Date()
            this.event.date_from = new Date()
            this.event.active_registration = false
            this.event.url_image = ''
        },
        deleteData(id, name, url_image) {

            
            this.$bvModal.msgBoxConfirm('Are you sure want to delete: ' + name + ' ?')
            .then(value => {
                if (value) {
                  //validate and delete if mettings exist

                    db.collection('event').doc(id).collection('meeting').get().
                        then(sub => {
                        if (sub.docs.length > 0) {
                            this.$bvModal.msgBoxOk('There are active breakout sessions to the selected Event', {
                                title: 'The action cannot be completed',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                        }
                        else {
                            
                            //Delete the image in case of exisiting

                            
                            if (url_image != '') {
                                //deelete image
                                firebase.storage().ref(`${id}`).delete()
                            }

                            db.collection('event')
                                .doc(id)
                                .delete()
                                .then(function() {
                                    console.log("Document deleted with ID - deleteData(): ", id)
                                })
                                .catch(function(error) {
                                    console.error("Error deleting document - deleteData(): ", error)
                                })
                        

                            this.$bvModal.msgBoxOk('Document was deleted successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                            //this.ok_delete = false
                        }

                })
                 .catch(function(error) {        
                    console.error("Error ValidateMeetings() ", error)
                })  

                }
            })
            .catch(err => {
                console.error("Error deleting document - deleteData(): ", err)
            })
        }
    }
}
</script>