<template>
    <ValidationObserver ref="observer" v-slot="{ handleSubmit }">
        
        <b-modal id="modalMeetingEdit" ref="modalMeetingEdit" hide-footer title="Edit Breakout Session" >
            <b-container fluid>
                <b-form @reset="onReset" @submit.stop.prevent="handleSubmit(onSubmit)">
                     <b-row>
                         <b-col sm="3">
                            Name
                        </b-col>
                        <b-col sm="9">
                            <validation-provider
                                name="Name"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-input
                                    id="input-name-meeting-edit"
                                    name="input-name-meeting-edit"
                                    v-model="meeting.name"
                                    placeholder="Name of Breakout Session"
                                    maxlength="50"
                                    :state="getValidationState(validationContext)"
                                    aria-describedby="input-1-live-feedback"
                                    trim
                                    autofocus
                                ></b-form-input>
                                <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                                
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row class="row mt-1">
                        <b-col sm="3">
                            Place
                        </b-col>
                        <b-col sm="9">
                            <validation-provider
                                name="Place"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-input
                                    id="input-place-meeting-edit"
                                    name="input-place-meeting-edit"
                                    v-model="meeting.place"
                                    placeholder="Place of Breakout Session"
                                    maxlength="50"
                                    :state="getValidationState(validationContext)"
                                    aria-describedby="input-1-live-feedback"
                                    trim
                                ></b-form-input>
                                <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                                
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row  class="row mt-1">
                        <b-col sm="3">
                            Date
                        </b-col>
                        <b-col sm="9">
                            <b-form-datepicker 
                                id="input-date-meeting-edit"
                                name="input-date-meeting-edit"
                                v-model="meeting.date"
                                aria-describedby="input-1-live-feedback"
                                value-as-date
                                locale="en"
                                trim 
                            />
                        </b-col>
                    </b-row>
                    <b-row class="row mt-1">
                        <b-col class="modal-footer">
                        </b-col>
                    </b-row>
                    <b-row  class="row mt-1">

                        <b-col class="text-right">
                            <b-button variant="success" type="submit" size="sm" v-if="meeting.id == '0'">Save changes</b-button>
                            <b-button variant="success" type="submit" size="sm" v-if="meeting.id != '0'">Apply changes</b-button>
                            <b-button variant="danger" type="reset" size="sm" >Reset</b-button>
                        </b-col>
                    </b-row>
                    
                </b-form>
            </b-container>
        </b-modal>
    </ValidationObserver>

</template>

<script>

   export default {
        name: 'ModalEditMeeting',
        props: ['event', 'meeting'],
        created() {
            this.$root.$on('bv::modal::show', (bvEvent, modalMeetingEdit) => {
                //if the Modal is show then start to get the data!

                
                if (this.meeting.id != '' && typeof this.meeting.id != 'undefined' && this.event.id != '0' && typeof this.event.id != 'undefined') {
                    //it's a edit
                    
                    
                    this.OpenMeetingForEdit(this.meeting.id)
                    
                }
                if (this.meeting_id = '0') {
                    this.meeting.name = ''
                    this.meeting.date_updated = ''
                    this.meeting.date = new Date() 
                }
                
            })
        },
        methods: {
           getValidationState({ dirty, validated, valid = null }) {
                return dirty || validated ? valid : null;
            },
            onSubmit() {
                if (this.meeting.id == '') {

                    db.collection('event').doc(this.event.id).collection('meeting').add({
                                date_updated: new Date(),
                                name: this.meeting.name,
                                date: this.meeting.date,
                                place: this.meeting.place
                        })
                        .then(function(docRef) {
                                console.log("Document written with ID - onSubmit() - AddEditMeeting", docRef.id)
                            })
                            .catch(function(error) {
                                console.error("Error adding document - onSubmit() - AddEditMeeting", error)
                            });
                    
                            this.$bvModal.msgBoxOk('Document was added successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })

                    this.$refs['modalMeetingEdit'].hide()
                } 

                if (this.meeting.id != '') {
                    let show_meeting_id = this.meeting.id
                    let setDoc = 
                        db.collection("event")
                            .doc(this.event.id)
                            .collection("meeting")
                            .doc(this.meeting.id)
                            .set({
                                date_updated: new Date(),
                                name: this.meeting.name,
                                place: this.meeting.place,
                                date: this.meeting.date
                            })
                            .then(function() {
                                console.log("Document written with ID - onSubmit() - SetEditMeeting", show_meeting_id)
                            })
                            .catch(function(error) {
                                console.error("Error writing document - onSubmit() - SetEditMeeting", error)
                            })

                        this.$bvModal.msgBoxOk('Document was edited successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })

                        this.$refs['modalMeetingEdit'].hide()
                }
            },
            onReset(evt) {
                evt.preventDefault()
                // Reset our form values
                this.meeting.name = ''
                this.meeting.place = ''
                this.meeting.date =  new Date()
            },
            OpenMeetingForEdit(id) {
                if (this.meeting.id != '' && this.event.id != '0') {
                    db.collection('event').doc(this.event.id)
                        .collection('meeting')
                        .doc(id)
                        .get()
                        .then(
                            snapshot => {
                                if (!snapshot.exists) return
                                let data = snapshot.data() //
                                this.meeting.name = data.name
                                this.meeting.place = data.place
                                this.meeting.date = data.date.toDate()
                            }
                    )
                }    
            }
        }
    }
</script>