<template>

    <ValidationObserver ref="observer" v-slot="{ handleSubmit }">
        <b-modal id="modalEditEvents" ref="modalEditEvents" hide-footer title="Edit Event" >
            <b-container fluid>
                <b-form @reset="onReset" @submit.stop.prevent="handleSubmit(onSubmit)">
                    <b-row>
                        <b-col sm="3">
                            Event
                        </b-col>
                        <b-col sm="9">
                            <validation-provider
                                name="Name"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-input
                                    id="input-event-name"
                                    ref="input-event-name"
                                    name="input-name-event-edit"
                                    v-model="event.name"
                                    placeholder="Name of Event"
                                    maxlength="50"
                                    :state="getValidationState(validationContext)"
                                    aria-describedby="input-1-live-feedback"
                                    trim
                                    autofocus
                                ></b-form-input>
                                
                                <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                            </validation-provider>
                        </b-col>
                    </b-row>
                    <b-row  class="row mt-1">
                        <b-col sm="3">
                            Reason
                        </b-col>
                        <b-col sm="9">
                            <validation-provider
                                name="Reason"
                                :rules="{ required: true, min: 3 }"
                                v-slot="validationContext"
                            >
                                <b-form-input
                                    id="input-reason-event"
                                    name="input-name-event-edit"
                                    v-model="event.reason"
                                    placeholder="Reason of Event"
                                    maxlength="50"
                                    :state="getValidationState(validationContext)"
                                    aria-describedby="input-1-live-feedback"
                                    trim
                                ></b-form-input>
                                <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                            </validation-provider>
                        </b-col>
                    </b-row>
                   <b-row class="row mt-1">
                        <b-col sm="3">
                            Date To
                        </b-col>
                        <b-col sm="9">
                            <b-form-datepicker 
                                id="input-dateto-event-edit"
                                name="input-dateto-event-edit"
                                v-model="event.date_to"
                                aria-describedby="input-1-live-feedback"
                                value-as-date
                                locale="en"
                                trim 
                            />
                        </b-col>
                    </b-row>
                   <b-row  class="row mt-1">
                        <b-col sm="3">
                            Date From
                        </b-col>
                        <b-col sm="9">
                            <b-form-datepicker 
                                    id="input-datefrom-event-edit"
                                    name="input-datefrom-event-edit"
                                    v-model="event.date_from"
                                    aria-describedby="input-1-live-feedback"
                                    value-as-date
                                    locale="en"
                                    trim 
                                />
                        </b-col>
                    </b-row>
                    <b-row class="row mt-3">
                        <b-col >
                            <p>Event image</p>
                            <input type="file" @change="previewImage" accept="image/*" >
                            <!-- <div>
                                <p>Progress: {{uploadValue.toFixed()+"%"}}
                                <progress id="progress" :value="uploadValue" max="100" ></progress>  </p>
                            </div> -->
                        </b-col>
                        <b-col>   
                            <div v-if="event.url_image!=''" id="preview">
                                
                                <img class="preview" :src="event.url_image">
                                <!-- </div>
                                <div>
                                <a href="#" @click="clearImage" class="btn btn-danger stretched-link">X</a>
                                </div> -->
                            </div>
                            
                            <!-- <button @click="onUpload">Upload</button> -->
                        </b-col>
                    </b-row>
                    <b-row class="row mt-1">
                        <b-col>
                            <b-form-checkbox
                                id="checkbox-active_registration"
                                v-model="event.active_registration"
                                name="checkbox-active_registration"
                                value=true
                                unchecked-value=false
                                >
                                <b class="text-info">Active for Registration</b>
                                </b-form-checkbox>
                        </b-col>
                    </b-row>
                    <b-row class="row mt-1">
                        <b-col class="modal-footer">
                        </b-col>
                    </b-row>
                    <b-row class="row mt-1">
                        <b-col>
                            <b-button variant="success" class="mr-2" type="submit" size="sm" v-if="event.id == '0'">Save changes</b-button>
                            <b-button variant="success" class="mr-2" type="submit" size="sm" v-if="event.id != '0'">Apply changes</b-button>
                            <b-button variant="danger" class="mr-2" type="reset" size="sm" >Reset</b-button>
                        </b-col>
                    </b-row>
                </b-form>
            </b-container>
        </b-modal>
    </ValidationObserver>   
</template>

<script>
    import moment from 'moment'
    import firebase from 'firebase'

    export default {
         
        name: 'ModalEditEvent',
        data() {
            return {
                uploadValue: 0,
                imgSelected: '',
                actual_url_image: ''
            }
            
        },
        props: ['event'],
        created() {

            

            this.$root.$on('bv::modal::show', (bvEvent, modalEditEvents) => { 

               if (this.event.id != '0' && typeof this.event.id != 'undefined') {
                    db.collection('event').doc(this.event.id)
                        .get()
                        .then(
                            snapshot => {
                                if (!snapshot.exists) return
                                let data = snapshot.data() //
                                this.event.name = data.name
                                this.event.reason = data.reason
                                this.event.date_to = data.date_to.toDate()
                                this.event.date_from = data.date_from.toDate()
                                this.event.active_registration = data.active_registration
                                this.event.url_image = data.url_image
                                this.actual_url_image = data.url_image
                            }
                        )
                }
                
                //this.imgSelected = ''
               
            })
            
             
        },
        methods: {
            getValidationState({ dirty, validated, valid = null }) {
                return dirty || validated ? valid : null;
            } ,
            onSubmit() {
                //if the id is diffentent to '0' edit
                if (this.event.id != '0') {
                    //if the event has a image in a url

                    //if the original value is != '' then delete the image
                    if (this.actual_url_image != '' && this.imgSelected != '') {
                        //deelete image
                        firebase.storage().ref(`${this.event.id}`).delete()
                        this.actual_url_image = ''
                    }
                    
                    
                    //if a new image is selected
                    if (this.imgSelected != '') {
                        /* Variables beacause I lost the reference after the process to upload the image */
                        let event_name=this.event.name
                        let event_reason=this.event.reason
                        let event_date_from=this.event.date_from
                        let event_date_to=this.event.date_to

                        
                        const storageRef=firebase.storage().ref(`${this.event.id}`).put(this.imgSelected);
                            storageRef.on(`state_changed`,snapshot=>{
                                this.uploadValue = (snapshot.bytesTransferred/snapshot.totalBytes)*100;
                            }, error=>{console.log(error.message)},
                            ()=>{this.uploadValue=100;
                                storageRef.snapshot.ref.getDownloadURL().then((url)=>{
                                        //the url image is sended to be edited
                                        this.event.name = event_name
                                        this.event.reason = event_reason
                                        this.event.date_from = event_date_from
                                        this.event.date_to = event_date_to
                                        this.editEvent(url)
                                     
                                    })
                                });
                    }
                    else {
                            //the image is no selected and send the same image 
                            this.editEvent(this.event.url_image)
                    }

                     

                    this.$bvModal.msgBoxOk('Document was edited successfully', {
                        title: 'Confirmation',
                        size: 'sm',
                        buttonSize: 'sm',
                        okVariant: 'success',
                        headerClass: 'p-2 border-bottom-0',
                        footerClass: 'p-2 border-top-0',
                        centered: true
                        })
                        .then(value => {
                            this.boxTwo = value
                        })
                        .catch(err => {
                            // An error occurred
                        })
                }
                //it is a new event
                if (this.event.id == '0') {
                    //add the event
                    let img_selected = this.imgSelected

                    //console.log(this.imgSelected)

                    db.collection('event').add({
                                date_updated: new Date(),
                                name: this.event.name,
                                reason: this.event.reason,
                                date_to: this.event.date_to,
                                date_from: this.event.date_from,
                                active_registration: (this.event.active_registration == "1" ? true : false),
                                url_image: ''
                            }
                        )
                        .then(docRef => {
                            
                            //console.log(this.imgSelected)

                             //after insert. if image is selected then update image...
                            if (img_selected != '') {

                                //console.log(this.imgSelected)
                                this.addImageAndUpdateURL(docRef.id, img_selected)
                                this.imgSelected = ''
                            } 
                            else {
                                console.log("Doscument written with ID - addData-without image(): ", docRef.id)
                            }
                        })
                        .catch(function(error) {
                            console.error("Error adding document - addData(): ", error)
                        })
                        
                
                        this.$bvModal.msgBoxOk('Document was added successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                               
                }

                this.imgSelected = ''
               this.$refs['modalEditEvents'].hide()
            },
            previewImage(event) {
                const file = event.target.files[0];
                this.uploadValue=0
                this.event.url_image=null
                //IMAGE selected to save
                this.imgSelected = event.target.files[0]
                this.event.url_image = URL.createObjectURL(file);
            },
            onReset(evt) {
                evt.preventDefault()
                this.event.name = ''
                this.event.reason = ''
                this.event.date_to =  new Date()
                this.event.date_from =  new Date()
                this.event.active_registration = false
                this.event.url_image = ''
            },
            editEvent(url_to_save) {
                let show_event_id = this.event.id
                let setDoc = 
                    db.collection("event")
                        .doc(this.event.id)
                        .set({
                            date_updated: new Date(),
                            name: this.event.name,
                            reason: this.event.reason,
                            date_to: this.event.date_to,
                            date_from: this.event.date_from,
                            active_registration: (this.event.active_registration == "true" ? true : false),
                            url_image: url_to_save
                        })
                        .then(function() {
                            console.log("Document written with ID - onSubmit() - SetEvent", show_event_id)
                        })
                        .catch(function(error) {
                            console.error("Error writing document - onSubmit() - SetEvent", error)
                        })
            },
            addImageAndUpdateURL(id_added, img_selected) {
                    //add image
                 const storageRef=firebase.storage().ref(`${id_added}`).put(img_selected);
                    storageRef.on(`state_changed`,snapshot=>{
                        this.uploadValue = (snapshot.bytesTransferred/snapshot.totalBytes)*100;
                    }, error=>{console.log(error.message)},
                    ()=>{this.uploadValue=100;
                        storageRef.snapshot.ref.getDownloadURL().then((url)=>{
                                    //the url image is sended to be added
                                

                                ///update URL
                                db.collection("event")
                                    .doc(id_added)
                                    .set({
                                        name: this.event.name,
                                        reason: this.event.reason,
                                        date_to: this.event.date_to,
                                        date_from: this.event.date_from,
                                        active_registration: (this.event.active_registration == "1" ? true : false),
                                        url_image: url
                                    })
                                    .then(function() {          
                                        console.log("Document written with ID - addImageAndUpdateURL(): ", id_added)
                                    })
                                         
                            })
                        }); 
            }
        }        
    }
</script>

<style scoped>

    #preview {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    #preview img {
        max-width: 100%;
        max-height: 200px;
    }
</style>