<template>
    <div>
        <ListOfEvents :events="events" :event="event" />
    </div>

</template>


<script>

    import moment from 'moment'
    import ListOfEvents from '../components/ListOfEvents.vue'
    

    export default {
        
        name: 'AIIASEvents',
        components: {
            ListOfEvents
        },
        created() {
            this.fetchEvents()
        },
        data() {
            return {
                events: [],
                
                event: {
                    name: '',
                    name_show: '',
                    reason: '',
                    date_to: null,
                    date_from: null,
                    date_show_to: null,
                    date_show_from: null,
                    active_registration: false,
                    url_image:''
                }
            }
        },
        methods: {
            //this fetch is necessary to repeat for every table in each file.
            fetchEvents() {
                
                db.collection('event').orderBy('name', 'desc').onSnapshot((querySnapshot) => {
                    let allEvents=[]
                
                    querySnapshot.forEach(doc => {
                        const dbEvents = {
                            //use de show beacause de edit form, visualization matters
                            id: doc.id,
                            name: doc.data().name,
                            name_show: doc.data().name,
                            reason: doc.data().reason,
                            //date_to: doc.data().date_to.toDate(),
                            date_show_to: moment(doc.data().date_to.toMillis()).format('ll'),
                            date_show_from: moment(doc.data().date_from.toMillis()).format('ll'),
                            url_image: doc.data().url_image
                        }
                        allEvents.push(dbEvents)
                    })
                    this.events=allEvents
                })
            }
        }
    }
</script>