<template>
    
    <!--  class="modal fade" id="modalMeetings" tabindex="-1" role="dialog" aria-labelledby="editLabel" aria-hidden="true" ref="vuemodal" -->
    <b-modal size="lg" id="modalMeetings" hide-footer title="Breakout Sessions" >
        <b-container fluid>
            <b-row>
                <b-col class="text-center">
                    <h5>Event: {{event.name}}</h5>
                </b-col>
            </b-row>
            <b-row>
                <b-col class="text-center">
                    <b-form-group
                        label="Filter"
                        label-cols-sm="1"
                        label-align-sm="left"
                        label-size="sm"
                        label-for="filterInput"
                    >
                        <b-input-group size="sm">
                            <b-form-input
                                v-model="filter"
                                type="search"
                                id="filterInput"
                                placeholder="Type to Search"
                            ></b-form-input>
                    
                            <b-input-group-append>
                                <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
            </b-row>
            <b-row>
                <b-col>
                    <!-- pagination component  -->
                    <b-pagination
                        v-model="currentPage"
                        :total-rows="rows"
                        :per-page="perPage"
                        aria-controls="my-table-meetings"
                        align="left"
                        label="Sort"
                    >
                    </b-pagination>
                </b-col>
                <b-col class="text-left"> 
                    <p>Current Page: {{ currentPage }}</p>
                </b-col>
                <b-col> 
                    <div class="text-right">
                        <!-- Pass modal that will be visualized and pass the first component to be focused -->
                        <b-button variant="success"  size="sm" @click="setMeetingEditID('')" v-b-modal.modalMeetingEdit>
                            Add Breackout Session
                        </b-button>
                    </div>
                </b-col>
            
            </b-row>
            <b-row>
                <b-col>
                    <!-- main table -->
                    <b-table
                        class="text-left"
                        id="my-table-events"
                        :items="meetings"
                        :fields="fields"
                        :per-page="perPage"
                        :current-page="currentPage"
                        :sort-by.sync="sortBy"
                        :sort-desc.sync="sortDesc"
                        :filter="filter"
                        :filterIncludedFields="filterOn"
                        @filtered="onFiltered"
                        sort-icon-left
                        responsive="sm"
                        small
                        
                    >
                        <!-- Buttons -->
                        <template v-slot:cell(actions)="meetings">
                            <b-button  variant="primary" size="sm" @click="setMeetingEditID(meetings.item.id)" v-b-modal.modalMeetingEdit >
                                Edit
                            </b-button> |
                            <b-button  variant="danger" size="sm" v-on:click="deleteMeeting(meetings.item.id, meetings.item.name)">
                                Delete
                            </b-button>
                        </template>
                    </b-table>                    
                </b-col>
            </b-row>
            <b-row>
                <b-col class="modal-footer">
                    
                </b-col>
            </b-row>
        </b-container>
        
        <ModalEditMeeting :event="event" :meeting="meeting"  />
        
    </b-modal> 
</template>

<script>
import moment from 'moment'
import ModalEditMeeting from '../components/ModalEditMeeting.vue'

export default {
    
    components: {
        ModalEditMeeting
    },
    name: 'ModalMeeting',
   props: ['event'],
    data() {
        return {
            filter: null,
            filterOn: [],    
            sortBy: 'Name',
            sortDesc: false,
            perPage: 5,
            currentPage: 1,
            
            meetings: [],
                
            meeting: {
                name: '',
                place: '',
                date: null
            },
            fields: [
                {
                    key: 'name', 
                    label: 'Name',
                    sortable: true,
                    sortDirection: 'desc',
                    fixed: true 

                },
                {
                    key: 'date', 
                    label: 'Date',
                    sortable: true
                },
                {
                    key: 'place', 
                    label: 'Place',
                    sortable: false
                },
                {
                    key: 'actions', 
                    label: 'Actions'
                }
            ]
        }
    },
    created() {
        this.$root.$on('bv::modal::show', (bvEvent, modalMeetings) => {
            //if the Modal is show then start to get the data!

            if (this.event.id != '0' && typeof this.event.id != 'undefined') {
                db.collection('event')
                    .doc(this.event.id)
                    .collection('meeting')
                    .orderBy('date', 'desc').onSnapshot((querySnapshot) => {
                    let allMeetings=[]
                
                    querySnapshot.forEach(doc => {

                        const dbMeetings = {
                            id: doc.id,
                            name:  (doc.data().name).slice(0, 25),
                            place: (doc.data().place).slice(0, 25),
                            date: moment(doc.data().date.toMillis()).format('ll')
                        }
                        allMeetings.push(dbMeetings)
                    })
                    this.meetings=allMeetings
                })
            }
        })
    },   
    computed: {
        rows() {
            return this.meetings.length
        }
    },
    methods: {
        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRoaws = filteredItems.length
            this.currentPage = 1
        },
        setMeetingEditID(id) {
            this.meeting.id = id
            this.meeting.name = ''
            this.meeting.place = ''
            this.meeting.date = new Date()
        },
        deleteMeeting(meeting_id, name) {
            this.$bvModal.msgBoxConfirm('Are you sure want to delete: ' + name + ' ?') 
            .then(value => {
                if (value) {
                    db.collection('event')
                        .doc(this.event.id)
                        .collection('meeting')
                        .doc(meeting_id)
                        .delete()
                        .then(function() {
                            console.log("Document deleted with ID - DeleteMeeting()", meeting_id)
                        })
                        .catch(function(error) {
                            console.error("Error deleting document - DeleteMeeting()", error)
                        })
                        
                    this.$bvModal.msgBoxOk('Document was deleted successfully', {
                                title: 'Confirmation',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'success',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                }
            })
            .catch(err => {
                console.error("Error deleting document - DeleteMeeting() ", error)
            })
                
        }
    }
    
}
</script>